import React from "react";
import { RouteComponentProps } from "react-router-dom";
import "./player.css"

interface IReactRouterParams {
    url: string;
}

export class Player extends React.Component<RouteComponentProps<IReactRouterParams>, React.ComponentState> {
    render() {
        return (
            <div className="ns-player">
                <video src={decodeURIComponent(this.props.match.params.url)} controls autoPlay></video>
            </div>
        );
    }
}
