import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Chat from "../../pages/twitch/chat";
import Player from "../../pages/player";

import "./App.css";

export class App extends React.Component {
    render() {
        return (
            <Router>
                <Switch>
                    <Route path={process.env.PUBLIC_URL + "/uh"} render={(props) => <div>UH</div>} />
                    <Route path={process.env.PUBLIC_URL + "/player/:url"} render={(props) => <Player {...props} />} />
                    <Route path={process.env.PUBLIC_URL + "/twitch/chat"}>
                        <Chat />
                    </Route>
                    <Route path="/">
                        <div>hehe</div>
                    </Route>
                </Switch>
            </Router>
        );
    }
}
